html {
  height: 100%;
  font-size: 14px;
}
body {
  font: 14px/1.5 Helvetica Neue, Helvetica, PingFang SC, Hiragino Sans GB, Microsoft YaHei, Noto Sans CJK SC, WenQuanYi Micro Hei, Arial, sans-serif;
  background-color: #f5f6f7;
  color: #34495e;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
  background-image: url(/static/images/bg.jpg);
}
a {
  color: #34495e;
  text-decoration: none;
}
a:hover {
  color: #409eff;
}
ul {
  padding: 0;
  margin: 0;
}
li {
  list-style: none;
}
button {
  font-family: "Microsoft YaHei", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
}
.load-loop {
  animation: load-loop 1s linear infinite;
}
/* 返回顶部按钮样式 */
.back-top {
  position: fixed;
  display: block;
  right: 16px;
  bottom: 56px;
  width: 40px;
  height: 40px;
  line-height: 40px;
  border-radius: 4px;
  background-color: #e7eaf1;
  cursor: pointer;
  color: #8590a6;
  text-align: center;
}
.back-top .Icon {
  vertical-align: text-bottom;
  fill: #9fadc7;
}
.back-top:hover {
  background: #d5dbe7;
}
.bg-oc {
  width: 100%;
  height: 100%;
  background: #eee;
}
.mask {
  border-color: rgba(242, 242, 242, 0.1);
  background-color: rgba(242, 242, 242, 0.5);
}
