.app {
  width: 970px;
  position: relative;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  height: 100%;
  min-height: 100%;
  /* 主布局 */
  /* 头部布局 */
  /* 动态内容主要显示区域 */
  /* 底部 */
}
.app-github {
  display: inline-block;
  width: 2rem;
  height: 2rem;
  border-radius: 50%;
  text-align: center;
  padding-top: .375rem;
  text-decoration: none;
  transition: all .2s;
  color: #525251;
  cursor: pointer;
}
.app-github i {
  font-size: 25px;
}
.app-cover {
  position: fixed;
  top: 0;
  bottom: 0;
  z-index: 1024;
  width: 200px;
  display: flex;
  background-color: #fff;
  flex-direction: column;
  box-shadow: 0 0 2px 0 rgba(204, 208, 213, 0.75);
  color: #fff;
}
.app-cover-bg {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  width: 100%;
  z-index: -100;
  background: url(/static/images/cover.jpg) 50% 50% / cover no-repeat;
}
.app-cover-info {
  display: flex;
  width: 100%;
}
.app-card {
  margin-bottom: 15px;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
  border-radius: 2px;
  left: 0;
  top: 20px;
  right: 0;
  color: #fff;
  position: absolute;
}
.app-card-title {
  font-size: 14px;
  padding: 12px 20px;
}
.app-author {
  text-align: center;
}
.app-author-pic {
  display: block;
  padding: 2px;
  margin-right: 10px;
  width: 90px;
  height: 90px;
  margin: 0 auto;
  border-radius: 50%;
}
.app-author-name {
  margin: 0;
  font-weight: 500;
  font-size: 16px;
}
.app-author-description {
  margin-top: 0;
  line-height: 1.6;
  font-size: 13px;
}
.app-state {
  text-align: center;
  overflow: hidden;
  line-height: 1.4;
  white-space: nowrap;
  padding-bottom: 20px;
}
.app-state-item {
  display: inline-block;
  padding: 0 15px;
  border-left: 1px solid #ccc;
}
.app-state-item:first-child {
  border-left: none;
}
.app-state-count {
  display: block;
  text-align: center;
  color: inherit;
  font-weight: 600;
  font-size: 16px;
}
.app-state-name {
  font-size: 13px;
  color: #999;
}
.app-content {
  margin-top: 40px;
  position: relative;
  display: flex;
  flex: 1 0 auto;
  flex-direction: column;
  height: 100%;
  min-height: 100%;
}
.app-header {
  background-color: #fff;
  z-index: 999;
  flex: 1 0 auto;
  padding: 5px 20px;
  box-shadow: 0 2px 2px rgba(0, 0, 0, 0.05), 0 1px 0 rgba(0, 0, 0, 0.05);
  position: fixed;
  top: 0;
  width: 100%;
  max-width: 970px;
  align-items: center;
  display: flex;
}
.app-header-title {
  color: #71777c;
  line-height: 24px;
  transition: color .15s ease;
  font-weight: 300;
  letter-spacing: 0.9px;
  margin-right: 22px;
  font-size: 14px;
}
.app-header .header__nav {
  flex: 1 0 auto;
}
.app-header-nav {
  display: flex;
  align-items: center;
  height: 100%;
  margin: 0;
  flex: 1 0 auto;
}
.app-header-main {
  flex: 1 0 auto;
}
.app-header-main a {
  color: #71777c;
  transition: color .15s ease;
  display: inline-block;
  vertical-align: middle;
  font-weight: 300;
  letter-spacing: 1.2px;
  margin-right: 20px;
  font-size: 14px;
}
.app-header-main a:hover {
  color: #61B3E6;
}
.app-header-main a:nth-child(10) {
  margin-right: 0;
}
.app-header-search {
  margin-top: 5px;
  margin-bottom: 5px;
  border: 1px solid rgba(150, 150, 150, 0.2);
  background-color: rgba(227, 231, 236, 0.2);
  display: flex;
  align-items: center;
}
.app-header-search-icon {
  cursor: pointer;
  fill: #aeb6c0;
  margin: 0 10px;
}
.app-header-search-input {
  border: none;
  box-shadow: none;
  outline: none;
  color: #aeb6c0;
  background-color: transparent;
  font-size: 12px;
  padding: 7px 12px;
  width: 180px;
}
.app-main {
  display: flex;
  flex-direction: row;
  flex: 1 0 auto;
  margin-top: 50px;
}
.app-sidebar {
  display: flex;
  width: 240px;
  background-color: #fff;
  padding-top: 10px;
  flex-direction: column;
}
.app-widget {
  overflow: hidden;
  width: 100%;
}
.app-widget-header {
  padding: 5px 10px;
  font-size: 14px;
  color: #969696;
  font-weight: 400;
}
.app-widget-article-list {
  padding: 0 20px;
}
.app-widget-article-item {
  position: relative;
  padding: 6px 0;
}
.app-widget-category-list {
  padding-left: 16px;
  padding-top: 5px;
  padding-bottom: 5px;
}
.app-widget-category-list span {
  font-family: Georgia, serif;
}
.app-widget-category-item {
  padding: 4px 0;
}
.app-widget-category-item > a {
  padding: 5px 8px;
}
.app-footer {
  text-align: center;
  flex: 0 0 auto;
  padding: 8px 0 10px 20px;
  border-top: 1px dashed #d9d9d9;
  overflow: hidden;
  color: #999;
  box-sizing: border-box;
  font-family: "lucida grande", "lucida sans unicode", lucida, helvetica, "Hiragino Sans GB", "Microsoft YaHei", "WenQuanYi Micro Hei", sans-serif;
  font-size: 12px;
  font-weight: normal;
  line-height: 20px;
  background-color: #fff;
}
.app-footer p {
  margin: 0;
  line-height: 20px;
}
.page-list-nav {
  padding: 20px 40px;
  text-align: center;
}
.page-list-nav a {
  margin: 0 1em;
  cursor: pointer;
  font-weight: 600;
  color: #4f76e4;
}
.page-list-nav span {
  font-weight: 600;
}
.page-list-nav .disabled {
  color: #ccc;
}
.articles {
  display: flex;
  flex: 1 0 auto;
}
.article-list {
  width: 730px;
  background-color: #fff;
  border-right: 1px dashed #ccc;
}
.article-item {
  padding: 10px 15px;
}
.article-item:last-child {
  border-bottom: none;
}
.article-header {
  display: flex;
  margin: 10px 0 10px;
  justify-content: space-between;
}
.article-brief {
  line-height: 32px;
  font-size: 18px;
  display: block;
  padding: 0 28px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.article-title {
  font-weight: 700;
  color: #555;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.article-title:hover {
  color: #007fff;
}
.article-meta {
  color: #999;
  font-size: 12px;
}
.article-meta a {
  color: #999;
}
.article-meta em {
  margin: 0 8px;
  color: #666;
}
.article-thumb {
  width: 150px;
  height: 96px;
}
.article-thumb a {
  display: block;
  width: 100%;
  height: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  padding: 0;
  margin: 0;
}
.article-summary {
  padding: 0 28px;
  word-wrap: break-word;
}
.article-masker {
  width: 730px;
  background-color: #fff;
}
.article-masker-row {
  padding: 10px 15px;
}
.article-masker-header {
  display: flex;
  margin: 10px 0 10px;
  justify-content: space-between;
}
.article-masker-brief {
  line-height: 32px;
  font-size: 18px;
  display: block;
  padding: 0 28px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.article-masker-meta {
  height: 20px;
  width: 200px;
}
.article-masker-title {
  margin-top: 10px;
  height: 20px;
  width: 250px;
  margin-bottom: 10px;
}
.article-masker-thumb {
  width: 150px;
  height: 96px;
}
.article-masker-summary {
  height: 150px;
  width: 700px;
}
.article {
  display: flex;
  flex: 1 0 auto;
}
.article__inner {
  margin-top: rem(10px);
  flex: 1 0 auto;
  display: flex;
  flex-direction: column;
  max-width: 730px;
  background-color: #fff;
  border-right: 1px dashed #ccc;
  padding: 10px 28px 0 28px;
}
.article__header {
  margin-bottom: 20px;
  text-align: center;
}
.article__title {
  font-size: 19px;
  word-break: break-word;
  margin: 10px 0;
}
.article__title-link {
  display: inline-block;
  position: relative;
  color: #555555;
  border-bottom: none;
  vertical-align: top;
}
.article__meta {
  margin: 3px 0px 6px;
  color: #999999;
  font-family: Monda, 'PingFang SC', 'Microsoft YaHei', sans-serif;
  font-size: 12px;
}
.markdown {
  font-size: 14px;
  line-height: 1.5;
}
.markdown h1,
.markdown h2,
.markdown h3,
.markdown h4 {
  min-height: 26px;
  line-height: 26px;
  margin: 20px 0;
  padding: 5px 20px;
  font-size: 14px;
  font-weight: 400;
  border-left: 3px solid #82c83c;
  color: #585957;
  background-color: #f9f9f9;
  text-shadow: 0 2px 1px rgba(255, 255, 255, 0.5);
}
.markdown p {
  word-wrap: break-word;
  font-size: 14px;
  line-height: 1.5;
}
.markdown img {
  max-width: 100%;
}
blockquote {
  border-left: 4px solid #DDD;
  margin: 20px 0;
  padding: 5px 20px;
  color: #777;
  background-color: #f9f9f9;
  font-style: italic;
}
