  margin-bottom: 0px;
}
.markdown ul,
.markdown ol {
  padding-left: 32px;
}
.markdown ul {
  list-style-type: circle;
}
.markdown ul li > :first-child,
.markdown ol li > :first-child,
.markdown ul li ul:first-of-type,
.markdown ol li ol:first-of-type,
.markdown ul li ol:first-of-type,
.markdown ol li ul:first-of-type {
  margin-top: 0px;
}
.markdown ul ul,
.markdown ul ol,
.markdown ol ol,
.markdown ol ul {
  margin-bottom: 0;
}
.comments__tip {
